// Muestra la hora tal como la escribiste en tus datos
function formatExactDate(dateString: string): string {
  // Si es un formato tiktokISO, convirtelo; si no, devulvelo limpio
  const parsed = new Date(dateString);
  if (!isNaN(parsed.getTime())) {
    return parsed.toLocaleString("es-ES", {
      day: "2-digit",
      month: "long",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  }
  // No intentamos parsear los textos en espaol, slo los devolvemos sin el InvalidDate
  return dateString;
}

function formatTimeAgo(dateString: string): string {
  const parsed = new Date(dateString);
  if (isNaN(parsed.getTime())) return ""; // no mostrar InvalidDate

  const now = new Date();
  const diff = Math.floor((now.getTime() - parsed.getTime()) / 1000);
  const rtf = new Intl.RelativeTimeFormat("es", { numeric: "auto" });

  if (diff < 60) return "hace unos segundos";
  if (diff < 3600) return rtf.format(-Math.floor(diff / 60), "minute");
  if (diff < 86400) return rtf.format(-Math.floor(diff / 3600), "hour");
  if (diff < 2592000) return rtf.format(-Math.floor(diff / 86400), "day");
  if (diff < 31536000) return rtf.format(-Math.floor(diff / 2592000), "month");
  return rtf.format(-Math.floor(diff / 31536000), "year");
}

