    id: 40,
    title: `Nicols Grande, el joven que reivindica la tauromaquia desde las redes: Mi generacin es el futuro de este arte`,
    image: "/images/nicolas.jpg",
    category: "Entrevistas",
    date: "9 de Diciembre de 2025",
    fullContent: `Con apenas unos aos de presencia en redes sociales, **Nicols Grande** se ha convertido en una de las voces jvenes ms visibles en defensa de la tauromaquia. Estudiante de veterinaria y apasionado del toro, su discurso rompe clichs: no viene de una familia taurina ni creci rodeado de corridas, pero encontr en los sanfermines el inicio de una fascinacin que marcara su camino.

Por eso, desde Portal Tendido Digital hemos querido entrevistarle para conocerle algo ms.

**Nicols, estudias veterinaria. Qu te llev a interesarte por la tauromaquia, y cmo concilias ese amor por los animales con la defensa del espectculo taurino?**

Mi verdadera pasin son los animales. El toro de lidia fue, desde el principio, lo que despert mi inters por este espectculo. Yo no vengo de una familia especialmente taurina, pero ver cada 7 de julio en las calles de Pamplona a esos animales majestuosos correr fue lo que me gener una fascinacin enorme.
Respecto a la defensa de la tauromaquia, desde fuera puede parecer algo muy complejo. Sin embargo, cuando uno entiende la fiesta brava y se dedica a estudiarla, descubre un mar infinito de argumentos que la sustentan. Solo hace falta acercarse con la mente abierta.

**Has ganado visibilidad en Instagram/TikTok como joven defensor de la tauromaquia. Cmo usas tus redes para comunicar tu visin? Crees que las redes pueden cambiar la percepcin de los toros entre la gente joven?**

Desde que empec en redes no he parado de aprender. Me adentr en un mundo que desconoca por completo; de hecho, ni siquiera tena TikTok: me lo descargu exclusivamente para empezar a crear contenido.
En un inicio quera hablar del mundo ganadero en general, ya que haba trabajado en una ganadera de carne en Cantabria y me apasionaba la defensa del medio rural. Pero un da decid subir un vdeo con argumentos a favor de la tauromaquia, y tuvo tanto xito que me replante mi vocacin.
Me di cuenta de que en redes faltaban creadores taurinos adaptados a los nuevos tiempos, capaces de llegar a un pblico joven. Ah decid enfocar mi contenido hacia una especie de evangelizacin de la tauromaquia desde un formato moderno.
Creo que antes era ms fcil consumir tauromaquia, y que durante un tiempo se dej de difundir este arte; pero gracias a las redes sociales puede volver a conectar con un pblico amplio.
Muchos asocian la tauromaquia con generaciones mayores.

**T representas una generacin joven a favor del toreo. Qu crees que puede aportar tu generacin a la tauromaquia? Qu inters ves hoy en jvenes por este mundo?**

Mi generacin es el futuro de todo. De nosotros depende la continuidad de nuestra cultura. Si no somos nosotros quienes ocupamos los tendidos, quin lo har?
Tenemos la responsabilidad de escuchar y aprender de nuestros mayores, de los toreros, de los escritores taurinos y de toda la sabidura que ellos han acumulado, para en un futuro poder transmitirlo.
Aun as, hay un aspecto que me tranquiliza: los jvenes empezamos a buscar referentes en una sociedad que muchas veces se percibe como vaca o sin hroes. En la tauromaquia muchos encuentran figuras valientes, personas que se juegan la vida por aquello que aman, mientras vemos a nuestros representantes polticos esconderse ante todo lo que no les beneficia.

**La tauromaquia est muy politizada, con defensores y detractores apasionados. Cmo valoras ese contexto? Piensas que hay una politizacin excesiva? Qu espacio crees que ha de tener la tradicin del toro en la sociedad actual? (Sobre todo por la zona de Barcelona)**

Es una pena que la cultura se politice. No ocurre solo con los toros: hoy en da prcticamente todo se usa para generar tensin y confrontacin.
Existen numerosos ejemplos de personajes pblicos que, independientemente de su ideologa, acuden a las plazas. Por mencionar algunos, tanto Isabel Daz Ayuso (del Partido Popular) como Joaqun Sabina (abiertamente socialista) disfrutan de la tauromaquia.
La fiesta no entiende de colores ni de partidos: es del pueblo y para el pueblo.
En cuanto a Barcelona, es triste ver cmo la familia Bala sigue con su juego cobarde de mantener cerradas las plazas. Catalua es taurina, y eso se refleja en los muchos pueblos de la comunidad donde se celebran festejos con ms libertad que en la propia ciudad.
Aun as, tengo esperanza de que, con la ayuda de mi amigo Mario Vilau, logremos reabrir la Monumental.

**Si tuvieras que explicar a alguien que nunca ha visto un toro de lidia por qu te gusta la tauromaquia, qu argumentos daras apelando a lo emocional, cultural o artstico?**

Le dira que es algo que, hasta que no lo vives, no lo puedes entender. Y aun vivindolo, sigue siendo difcil de explicar.
Me gusta compararla con un cuadro abstracto: o tienes la sensibilidad para apreciar el arte que encierra, o simplemente no lo ves. No hay trmino medio. Puede hacerte vibrar en un buen natural, o puede parecerte solo un hombre con una capa roja.
Aun as, creo que cualquiera debera sentarse en un tendido al menos una vez para construirse una opinin real sobre los toros.`,
