    id: 63,
    title: `Gran jornada de Faena y Doma de Campo en la Finca Heredade do Barroso, propiredad de D. Jos Luis Pereda Lpez`,
    image: "/images/granjornada.jpg",
	imageCaption: "Foto Juan Antonio Caro",
    category: "Actualidad",
    date: "2 de Diciembre de 2025",
    footerImage1: "/images/jornada1.jpg",
    footerImage2: "/images/jornada2.jpg",
    footerImage3: "/images/jornada3.jpg",
    footerImage4: "/images/jornada4.jpg",
    footerImage4Caption: "Fotos Juan Antonio Caro",
    fullContent: `Al filo de las 12:00 horas del pasado sbado, 29 de noviembre, dio comienzo en la Finca Herdade do Barroso propiedad de D. Jos Luis Pereda Lpez y situada en la comarca portuguesa del Alentejo una destacada jornada de disciplina hpica deportiva. Esta actividad, denominada actualmente Faena y Doma de Campo y conocida tradicionalmente como Acoso y Derribo, congreg a un nutrido y cualificado grupo de participantes, subrayando el carcter histrico y seero de esta labor ganadera.

La cita reuni a **50 colleras de garrochistas, organizadas en 10 grupos**, que protagonizaron una intensa y coordinada faena campera.
Para el desarrollo de esta prctica ancestral de manejo de reses en el campo, el anfitrin, D. Jos Luis Pereda, dispuso **55 reses herradas con el hierro de su casa y con el hierro de Doa Clotilde Lpez, madre del ganadero**.
A ello se sumaron **19 reses tambin corridas y derribadas procedentes de la ganadera invitada de D. Silvestre Macas**.
Todas las reses participantes fueron becerras y/o vacas cruzadas, conforme marca la tradicin en este tipo de labores.

Entre los aproximadamente **100 garrochistas** asistentes se dieron cita reconocidos especialistas y una amplia nmina de Campeones de Espaa, entre ellos: **Josele Caaveral, Jos Luis Dez de la Cortina, Alfonso Carlos Fernndez Gonzlez, Jos Mara Fernndez Fernndez, Alfonso Martn Garca El Pory, Manuel Carrera Butrn Carca, entre otros destacados nombres de la disciplina**.

Asimismo, el encuentro cont con la presencia de personalidades que representan la fusin entre la cultura taurina y la hpica deportiva. Entre ellos destacaron la figura del matador de toros **Daniel Luque**; **los representantes de la familia Campos Pea, Ernesto y Manolo Campos Moreno**; **Javier Buenda**, hijo del gran maestro del rejoneo; **Manolo Gonzlez**, hijo del reconocido ganadero; el **Campen de Espaa de Doma Vaquera Jos Ramn Garca Chamo**; **los picadores Diego Pea y Juan Francisco Pea**; as como el novillero con picadores **Sergio Domnguez El Mella**.

La jornada transcurri en un ambiente de excelencia tcnica y deportividad, en la que todos los intervinientes demostraron su pericia y habilidad. La supervisin tcnica corri a cargo de **D. Carlos Cid**, juez nacional de acoso y derribo, cuya labor garantiz el correcto desarrollo de la actividad en cada una de sus fases.

Finalmente, merece una mencin especial el impecable trabajo organizativo del anfitrin D. Jos Luis Pereda y de Manuel Sevillano Torres, actual mayoral de la ganadera de Pereda, cuya dedicacin result clave para el xito de esta jornada que reafirma el valor cultural, deportivo y tradicional de la Faena y Doma de Campo.`,
