    id: 76,
       title: "Expreso mi concepto bajo los cnones del clasicismo, con mi singularidad personal - Entrevista a David Galvn",
    image: "/images/entrevista.jpg",
    category: "Entrevistas",
    date: "29 de noviembre de 2025",
    fullContent: `**David Galvn**
encara uno de los momentos ms determinantes de su carrera. El matador gaditano, que con constancia, sensibilidad y una evolucin silenciosa ha pasado de ser una promesa a convertirse en un nombre respetado dentro del escalafn, atraviesa un proceso de madurez profesional que despierta ilusin entre la aficin.

Tras una temporada marcada por la solidez, actuaciones de gran calado y tardes en las que dej patente la profundidad de su concepto, Galvn ha logrado situarse como uno de los toreros con mayor poso y proyeccin. Su expresin clsica, su temple y una ambicin cada vez ms ntida lo consolidan como un perfil que merece ser escuchado.

**Cmo afronta usted la temporada que viene, teniendo en cuenta lo importante que ha sido esta?**

La temporada 2026 la afronto con la ilusin de dar mi mejor versin en cada actuacin, mostrar mi personalidad en su mxima dimensin y seguir sintiendo a la aficin ilusionada por ver a David Galvn. 

**Se ha creado un movimiento galvanista ya que el buen publico, admira que un concepto tan puro como el suyo, no cambie con corridas duras. Le gusta a usted que le encasillen con ese tipo de ganaderias o encastes? o preferiria torear corridas mas comodas y en otro tipo de carteles.**

Es muy bonito sentir ese movimiento Galvanista que he vivido este ao y sigo viviendo. Recibo el entusiasmo constante de aficionados de todas las edades de la geografa europea y americana, lo que supone una gran felicidad para m. 
Considero que no estoy encasillado en nada, no me pongo limitaciones, y es por este motivo que he conseguido desarrollar mi toreo y triunfar con todo tipo de ganaderas y encantes. 

**Per y Mxico, son dos paises con los que mantiene un idilio constante, y en los que se le espera con gran entusiasmo; que opina de estos dos paises? Y de los constantes ataques antitaurinos en mexico? ( se han vuelto a prohibir los toros en Ciudad Juarez)**

Tanto Per como Mxico son dos pases que llevo en mi corazn. Me encanta torear en ellos y sentir el calor de sus aficiones. Siempre tendrn mi apoyo incondicional. 

**Como quiere que se le recuerde, cuales son sus mayores aspiraciones en este mundo?**

Como artista y como persona lo que ms me llena es sentir que la gente se emociona y es feliz a travs de mi expresin, esta es la mayor satisfaccin y aspiracin. 

 **Su concepto del toreo ha sido definido muchas veces como clsico y eterno. Cree usted que en la actualidad, donde abundan estilos ms efectistas, sigue habiendo espacio para ese clasicismo? Qu mensaje quiere transmitir cada vez que se pone delante de un toro?**

Particularmente siento que los pblicos si se identifican con mi toreo. Expreso mi concepto bajo los cnones del clasicismo, con mi singularidad personal. Me gusta la originalidad en el ruedo y que no haya nada previsible ni encorsetado.

**En Espaa, la temporada pasada dej tardes memorables en plazas de primera. Qu importancia le da a triunfar en Madrid o Sevilla frente a otras plazas ms pequeas? Considera que el pblico de cada ciudad entiende y valora de manera distinta su tauromaquia?**

Mi filosofa como torero pasa por expresar mi toreo con la misma entrega y compromiso independientemente de la categora reglamentaria de la plaza. El pblico acude a la plaza y merece ver la mejor versin de uno mismo. 

En plazas de primera es cierto que ha habido faenas importantes en este ao 2025, en las que he sentido el reconocimientos de aficiones que son exigentes y dan crdito. Inolvidables han sido las faenas en Sevilla y Mlaga, el San Isidro de esta temporada o las tardes de capacidad en Dax y Albacete. 



**La juventud se acerca cada vez ms al toreo, pero tambin se enfrenta a crticas sociales. Qu consejo dara usted a los jvenes que suean con ser toreros, y cmo cree que deberan afrontar las presiones externas que cuestionan la fiesta?**

Que persigan su sueo con fe, sin complejos y sintindose libres. 

**El toro bravo es el eje de todo este mundo. Qu opinin tiene usted sobre la evolucin de las ganaderas actuales? Prefiere enfrentarse a hierros de mxima exigencia que ponen a prueba al torero, o cree que tambin es necesario buscar variedad para mostrar diferentes matices de su arte?**

El nivel de las ganaderas, cada una en su contexto y personalidad, en la actualidad es muy alto. Los ganaderos estn haciendo una gran labor. 
Para el aficionado considero que causa mayor inters la variedad que la monotona. Me preparo diariamente para tener registros suficientes para expresar mi toreo a todo tipo de toros independientemente de su condicin o ganaderia, siempre fiel a mi sello personal.`,
