       title: "David de Miranda, un torero con alma de nio en el Colegio Montaigne Compaa de Mara de Jerez",
    image: "/images/reemplazo.jpg",
    category: "Actualidad",
    date: "29 de noviembre de 2025",
    fullContent: `En la maana de ayer , viernes 28 de noviembre, el claustro mudjar el Colegio Montaigne Compaa de Mara, en Jerez de la Frontera, se convirti en el escenario de una jornada cargada de emocin, tradicin y aprendizaje. En este histrico espacio, el torero David de Miranda ofreci una interesante charla a los estudiantes, compartiendo sus experiencias y reflexiones sobre el toreo, su vida y su carrera. El evento, que cont con la moderacin del periodista scar Torres, de Onda Jerez, estuvo dirigido a nios y jvenes de entre 3 y 17 aos, quienes mostraron un notable inters por el mundo taurino.
 
La jornada se inici con la intervencin de la profesora y aficionada taurina Elena Aguilar Valderas, quien, adems de ser jurado del circuito de novilladas de las escuelas andaluzas, es docente en el colegio. Aguilar Valderas introdujo a los alumnos al mundo del toreo, destacando la
importancia de la cultura taurina en la ciudad de Jerez, considerada la Cuna del Toro Bravo. A continuacin, Francisco Marn, director del colegio, expres su satisfaccin por poder contar con la presencia de un torero de la talla de David de Miranda, cuya historia inspira tanto a los jvenes como a los adultos.
 
La charla, moderada por scar Torres, se centr en el universo del toreo y las vivencias de David de Miranda, quien se mostr cercano y sincero con los estudiantes. Durante su intervencin, el torero de Trigueros destac la importancia de la soledad en el ruedo, sealando que "el torero est solo frente al toro, pero esa soledad, aunque inquietante, es parte del rito, del sacrificio y del desafo". Con una mirada profunda, aadi que el miedo y la incertidumbre son inseparables del oficio, pero que "los sueos y las ilusiones son ms fuertes y superan cualquier temor".
 
David repas algunos de los momentos ms destacados de su carrera, como su impresionante debut en la Plaza de Las Ventas, -Puerta Grande en su confirmacin de Alternativa-, la Puerta de Prncipe de Sevilla, la histrica tarde de Mlaga, as como otras tardes memorables como Linares, Almera, Soria, Pozoblanco...Tambin comparti con los estudiantes la dura experiencia de la grave lesin que sufri en Toro (Zamora), un accidente que marc un antes y un despus en su carrera. Sin embargo, el diestro destac la resiliencia que le permiti superar ese difcil momento, y cmo el apoyo de su familia y de la aficin le ayud a recuperarse y seguir adelante.
 
El torero tambin habl con entusiasmo de su futuro en el toreo, mencionando su colaboracin con el maestro Enrique Ponce, uno de los grandes referentes del toreo mundial. "Nadie me ha regalado nada", afirm, "y eso es lo que me impulsa a seguir adelante con la misma mentalidad y actitud, tanto en lo profesional como en lo personal". Para David, el toreo es un arte nico, al igual que la infancia, y en ese sentido, inst a los jvenes a luchar por sus sueos para ser felices con las profesiones que elijan, sin rendirse ante las dificultades.
 
La jornada culmin con un emotivo acto en la Capilla de La Nia Mara, donde David de Miranda recibi la bendicin de una medalla de la compaa de la Virgen, un regalo que le hicieron los alumnos del colegio. Despus, el torero firm numerosos capotes y muletas, y tuvo la oportunidad de torear "in situ" con los jvenes de la escuela taurina del colegio, quienes demostraron una gran pericia en el manejo de los hbitos de torear. El onubense comparti esta experiencia con su hombre de plata Fernando Pereira que tambin aleccion a varios jvenes.
 
El Colegio Montaigne Compaa de Mara, con su escuela taurina en el patio-recreo, sigue cultivando el amor y respeto por la tradicin taurina y esta jornada con David de Miranda fue una oportunidad nica para que los nios y jvenes del centro se acercaran de primera mano a la vida de un torero.
 
Al final, todos compartieron la sensacin de que el toreo, ms all de ser un arte, es una pasin que se lleva en el corazn desde la infancia y que, como los sueos, nunca se debe dejar de perseguir.`,
