    category: "Actualidad",
    date: "28 de noviembre de 2025",
    fullContent: `Tras la emisin del documental en el mismo teatro se celebrar una mesa redonda en la que participarn el periodista Juan Belmonte, el poeta y compositor **Jos Len, Ralf Bunger y el torero David de Miranda**.
 
El documental Mondeo, el torero mstico ser presentado el prximo mircoles 3 de diciembre en el **centro municipal de cultura de Sanlcar La Mayor a las 19,30 horas.**
 
Tras su estreno en Sevilla y su presentacin en la Pea Francisco Montes Paquiro de Chiclana, la eleccin de esta localidad no es casual. El torero descansa para siempre en este pueblo, donde pas largas temporadas en su finca ubicada en el citado municipio junto a Ralf Bunger, su pareja sentimental hasta su muerte e hilo conductor del documental.

El proyecto, dirigido por Juan Belmonte y Rogelio M.Gordo, quiere ahora recalar en la que tambin consideraba su tierra con el fin de acercarle su vida a los que fueron sus vecinos. En concreto, sus ltimos aos los pas entre Sanlcar y Pars.
 
Juan Garca 'Modeo' no fue un torero al uso. Ni en el ruedo, ni en su da a da en la Espaa franquista de los aos 60. Sobre el albero fue considerado el eslabn entre la tauromaquia de Manolete con la de Jos Toms.
 
Y fuera tambin tuvo una singular trayectoria pues cuando estaba en la cima del toreo sinti la llamada de Dios y se meti a monje. Su toma de hbitos se convirti en un autntico acontecimiento nacional aireada incluso por el Nodo.
 
En la obra, el propio Mondeo cuenta los pormenores de su vida, su relacin con el mundo del toro, su decisin de entrar en una congregacin dominica y su vida junto al que fuera su amor: Ralf Bunger.
 
A su vez, el documental recoge su faceta ms familiar y la profesional, a travs de numerosas fotografas, reportajes de sus actuaciones en diversas plazas y testimonios de toreros de la poca como Curro Romero.
 
Tras su emisin, en el mismo teatro, se celebrar una mesa redonda en la que participarn el periodista Juan Belmonte, el poeta y compositor Jos Len, el propio Ralf y el torero David de Miranda.
 
**En las prximas fechas este documental ser emitido por Canal Sur Televisin, cadena que ha participado en la produccin.**`,
