    title: "Toms Rufo, Daniel Luque, Aarn Palacio y Cristiano Torres, protagonistas de la segunda edicin de los Premios Nicanor Villalta",
    image: "images/premiosnicanor.jpg",
    category: "Actualidad",
    date: "28 de Octubre de 2025",
    fullContent: `La segunda edicin de los Premios Nicanor Villalta, impulsados por el Gobierno de Aragn, reconoci este lunes a Toms Rufo, Daniel Luque, Aarn Palacio y Cristiano Torres como protagonistas de la temporada taurina 2025.
La ceremonia, celebrada en la Sala de la Corona del Edificio Pignatelli, reuni a autoridades y profesionales del mundo del toro en un acto que sirvi tambin para reivindicar el valor cultural y social de la tauromaquia.

El presidente aragons, Jorge Azcn, defini a los galardonados como cuatro toreros que han emocionado a los aficionados aragoneses esta temporada, y subray que la fiesta de los toros por mucho que a algunos les pese es arte, historia, cultura, tradicin, economa y defensa de la Espaa rural.

Por segundo ao consecutivo, el jurado distingui a Toms Rufo por su extraordinaria faena al sexto toro de la tarde del 13 de agosto en la Feria de la Albahaca de Huesca. En la Feria del Pilar de Zaragoza, el reconocimiento recay en Daniel Luque, premiado por su actuacin ante el quinto toro del 11 de octubre.

Asimismo, el jurado otorg ex aequo el galardn a la nueva generacin de toreros aragoneses a Aarn Palacio y Cristiano Torres, quienes tomaron la alternativa durante la temporada 2025.

Durante su intervencin, Azcn felicit a los premiados y destac su contribucin al toreo aragons. Toms, el ao que viene tienes que ir a por la mejor faena en Teruel y ser el primer torero que logra este premio en las tres provincias, seal. Sobre Luque, afirm que su compromiso con Zaragoza es innegable; es un torero de esta tierra y tiene a la aficin entregada. 

En cuanto a los jvenes premiados, asegur que Aarn y Cristiano representan la ilusin de una nueva etapa dorada para el toreo aragons.

El presidente aprovech la gala para reafirmar el apoyo institucional del Ejecutivo autonmico a la tauromaquia, recordando la recuperacin de las retransmisiones taurinas en Aragn TV, con 53 festejos emitidos y una cuota media de audiencia del 19,1%, casi el doble de la media de la cadena autonmica, que cerr 2024 con un rcord histrico del 11,6%.

Azcn evoc tambin la figura de Nicanor Villalta, considerado el torero aragons ms ilustre, y propuso organizar una corrida goyesca especial en 2027 para conmemorar el bicentenario de la muerte de Francisco de Goya.
Adems, dedic unas palabras de reconocimiento a Morante de la Puebla, recientemente retirado tras ms de tres dcadas de trayectoria, a quien defini como uno de los grandes toreros de la historia.

Como cierre de la velada, la Asociacin de Informadores Taurinos de Aragn otorg la Pluma de Oro al Gobierno de Aragn por la creacin de estos galardones, en reconocimiento a su impulso y compromiso con la fiesta. El premio fue recogido por Jorge Azcn y la vicepresidenta Mar Vaquero, en una jornada que reafirm la vigencia y el orgullo de la tradicin taurina en Aragn.`
