    category: "Entrevistas",
    date: "17 de Octubre de 2025",
    fullContent: `Carlos, en un momento donde la tauromaquia enfrenta tantos desafos sociales y polticos, qu significa para usted seguir apostando por este sector como empresario? 

Para mi es una forma de vida, una vocacin. Yo no s hacer otra cosa. Vivo 24 h para por y para el toro en su mxima expresin y no concibo el da a da sin ilusionarme y pensar en la confeccin de una feria. Creo que a veces puedo ser demasiado cansino en el da a da pero hasta ahora, esa "frmula" no me ha ido mal. Seguir peleando por y para el toreo y espero no desfallecer.

Gestiona plazas tan emblemticas como El Puerto, Gijn o Aranjuez. Qu criterios considera esenciales para que una feria taurina sea rentable y atractiva para el pblico? 

Creo que el secreto es dar al pblico de cada lugar lo que busca. Yo no me considero ni un Sneca ni un salvador del toreo, pero intento tener mi sello de calidad buscando la excelencia en el espectculo. Me gusta un determinado tipo de toro e intento no perder nunca el rigor y el trapo acorde a cada plaza. En Gijn, por ejemplo, llevo casi 25 aos con esa frmula y la Feria de Begoa est ms consolidada que nunca.

Qu le dira hoy a los polticos que impulsan prohibiciones o trabas a la celebracin de festejos taurinos en Espaa?

Simple y llanamente que respeten nuestras traiciones y las salvaguarden como garantes de un Bien declarado Patrimonio Cultural Inmaterial, por mucho que partidos radicales hayan tratado de boicotear.

Qu plaza suea con gestionar algn da, y qu aportara usted como empresario para devolverle o mantenerle su prestigio?

Bueno, imagnese, uno siempre suea con volar lo ms alto posible y en ese horizonte como no estn Sevilla y Madrid. Quien sabe si el futuro me deparar algn da algo bonito. Lo que aportara, me lo guardo para entonces.

La retirada de Morante de la Puebla marca un punto de inflexin en la tauromaquia actual. Tambin porque usted siempre lo ha contratado siempre que ha podido este ao en plazas como El Puerto de Santa Mara , Aranjuez.
Desde el punto de vista empresarial, cmo afecta la ausencia de una figura as en la confeccin de carteles y en la atraccin del pblico?

Reitero una vez ms mi agradecimiento pblicamente a Morante. Creo que ha sido el toreo ms grandioso que mis ojos han tenido la oportunidad de ver y que seguramente vayan a ver. Ha sido muy generoso con la Fiesta y especialmente con los empresarios. Con su apoyo y el de El Juli, he crecido profesionalmente y sus faenas histricas en mis plazas me han aportado un plus de calidad que nunca olvidar. Le echaremos mucho de menos como lo hemos hecho estos 2 aos con Julin.

Como plantea la temporada que viene de cara a nuevas plazas? Tiene en mente alguna? 

Estoy muy contento y muy feliz con las 9 o 10 plazas que gestiono actualmente pero soy una persona ambiciosa y adems la vida me ha enseado a saber esperar. Desde luego, y no lo voy a ocultar, mi objetivo es poder seguir creciendo pero siempre con la clarividencia de estudiar los pliegos y la situacin de cada plaza. Quin sabe, si el futuro, nos deparar alguna sorpresa. Tambin es bonito soar...`
