fullContent: `
La temporada del joven novillero alcorisano Toms Gonzlez ha sido un punto de inflexin, uno de los novilleros que ha demostrado con argumentos que puede estar en las ferias.

Cuenta con buenas actuaciones a lo largo de la temporada Vinaroz, Burgo de Osma, Mojados, Azuqueca de Henares, Zaragoza

En esta entrevista repasa su evolucin, sus momentos ms sealados del ao y las metas que lo motivan a seguir avanzando de cara a la temporada que viene.

1. Qu balances haces de tu temporada con importantes triunfos?

Ha sido una temporada muy importante, he crecido mucho como torero, que es lo ms importante para m. Hemos avanzado mucho, he encontrado la base de lo que quiero que sea mi toreo, que es algo fundamental.

2. Si tuvieras que sealar una faena y una plaza de este ao, dnde sera y por qu?

Me quedara por diferentes razones con la faena de mi primer novillo de Zaragoza. Un pinchazo previo a la estocada se llev el doble trofeo; me queda esa espina pero sent cosas muy bonitas e importantes en esa faena, me entregu y expres. Aunque ha habido faenas muy especiales, como las de Mojados, Marchamalo, Azuqueca, etc

3. Qu te ha enseado esta temporada como torero y como persona?

He aprendido muchas cosas: que cada paso lleva un proceso muy largo detrs y que todo cuesta mucho de conseguir; por eso hay que apreciar y saborear el proceso en el que te encuentras. Ser torero y poder materializarlo es un privilegio grandioso al que no le damos la relevancia que verdaderamente tiene. Tambin me ha ayudado a conocerme a m mismo; esta temporada se han vivido momentos realmente duros, que han hecho reafirmarme en mi vocacin torera como nunca.

4. Cul es tu estilo o qu estilo buscas?

No me gusta hablar de estilos, ms bien de conceptos. Mi intencin es la de torear como siento: encajado, relajado, lo ms despacio posible. Al final creo que es lo que ms lleva arriba, siendo siempre fiel a la personalidad de uno.

5. Cmo fue tu paso por Zaragoza tras una gran faena a un novillo de Pincha? Qu sentiste?

La tarde de Zaragoza fue muy especial; es la plaza en la que ms veces he estado en mi vida. Me sent realmente a gusto, disfrut, y eso en una plaza de esa relevancia es complicado. Creo que lo transmit arriba.

6. Cmo planteas tu prxima temporada?

Ahora es momento de reflexin, tomar conciencia de lo que ha ido sucediendo durante la temporada y utilizarlo para mejorar en el invierno. Aunque desde ya, esperando la temporada venidera y que venga cargada de tardes importantes.
`
